.container {
    width: 25%;
    display: flex;
    margin-left: 10px;
    justify-items: center;
    align-items: flex-start;
    gap: 15px;
    flex-direction: column;
    height: 100%;
}

.chatbox {
    height: 90%;
    width: 100%;
    overflow-y: scroll;
}

.sendBox {
    height: 10%;
    width: 100%;
    justify-items: center;
    align-items: center;
    gap: 10px;
}

.inputField {
    height: 100%;
    display: block;
    width: 80%;
}

.buttonSend {
    height: 100%;
    display: block;
    width: 20%;
    font-size: 0.7rem;
}
